<template>
  <div>
    <div class="login" v-if="!isLogin">
      <div class="title">简易聊天室</div>
      <input class="name-input" type="text" v-model="userName" placeholder="请输入你的名字" />
      <div class="tips">{{errTips}}</div>
      <button class="login-btn" @click="handleLogin">登录</button>
    </div>

    <div class="list" v-else>
      <div class="list-con">
        <div class="chat-list" refs="list">
          <p v-for="(item,index) in list" :key="index">
            <span>{{item.name}}说{{item.msg}}</span>
          </p>
        </div>
        <div class="user-list">
          <p v-for="(item,index) in userList" :key="index">{{item}}</p>
        </div>
      </div>
      <div class="send-btn">
        <input @keyup.enter="handleSend" v-model="msg" type="text" />
        <button @click="handleSend" @keyup.enter="handleSend">发送</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      userName: "",
      errTips: "",
      isLogin: false,
      list: [],
      userList: [],
      msg: ""
    };
  },
  mounted() {
    if (localStorage.getItem("name")) {
      this.isLogin = true;
      this.$socket.emit("chat list");
    }

    this.sockets.subscribe("login ok", name => {
      this.errTips = "登录成功了^_^";
      this.isLogin = true;
      localStorage.setItem("name", name);

      this.$socket.emit("chat list");
    });

    this.sockets.subscribe("is exist", () => {
      this.errTips = "已经有人用了^_^";
      this.userName = "";
    });

    this.sockets.subscribe("user list", list => {
      this.userList = list;
    });

    this.sockets.subscribe("is online", () => {
      this.$socket.emit("is online", localStorage.getItem("name"));
    });

    this.sockets.subscribe("chat msg", item => {
      this.list.push(item);
    });

    this.sockets.subscribe("chat list", list => {
      this.list = list;
    });
  },
  methods: {
    handleLogin() {
      this.errTips = "";
      if (!this.userName) {
        this.errTips = "请输出你的名字^_^";
        return;
      }

      // 告知服务端登录
      this.$socket.emit("login", {
        name: this.userName
      });
    },
    handleSend() {
      if (!this.msg) {
        return;
      }
      console.log(localStorage.getItem("name"));
      this.$socket.emit("chat msg", {
        msg: this.msg,
        name: localStorage.getItem("name")
      });

      this.msg = "";
    },

    handleScoll() {}
  }
};
</script>
